.App {
  width: 100%;
  height: 100%;
  aspect-ratio: 1/1;
  border-radius: 0.9em;
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  display: flex;
}

.Appbar{
  width: 30%;
  height: 100%;
  background-color: #f8f8f8;
  border-radius: 1em 0 0 1em;
  padding: 1rem 0 1rem 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  min-width: 360px;
  border-right: 1px solid rgba(62, 68, 69, 0.1);
}

.Appbody{
  flex: 1 1;
  height: 100%;
  background-color: #f8f8f8;
  border-radius: 0 1em 1em 0;
  overflow: hidden;
  position: relative;
  min-width: 360px;
  /* background-image: url('https://stupefied-meninsky-1b99f4.netlify.app/BackDrawingOriginal-withbg.eedd5bcd092c71ad6e80.png'); */
}

.Appbody::before{
  content: '';
  position: absolute;
  background-size: cover;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  /* z-index: 1; */
  opacity: 0.05;
  background-image: url('https://stupefied-meninsky-1b99f4.netlify.app/BackDrawingOriginal-withbg.eedd5bcd092c71ad6e80.png');
}


.icon{
  margin-bottom: 25%;
}

.not-selected-icon svg, .selected-icon svg{
  overflow: visible;
}

g#Petals, path#IconBG{
  display: none;
  transform-origin: center center;
  opacity: 0;
}

/* path#IconBG{
  fill: #f8f8f8;
} */

.selected-icon path#IconBG{
  display: block;
  fill: #f8846085;
